<template>
    <div class="container padrePasos" >
        
            <ul class="steps steps-5">
                <!-- <li  class="current"><a data-toggle="tab" href="#home"><em>Inicio</em>
                <span></span></a></li>
                <li><a data-toggle="tab" href="#menu1" ><em>Datos Trmite</em><span></span></a></li>
                <li ><a data-toggle="tab" href="#menu2" ><em>Datos Pago</em><span></span></a></li>
                <li><a href="#"><em>Fin</em><span></span></a></li> -->
                 
                <li v-for="paso of listaPasos" v-bind:class="[paso.idPaso.idPasos==pasoActual ? currentClass : '', errorClass]" :key=paso.idPaso.idPasos ><a data-toggle="tab" href="#home"><em>{{paso.idPaso.nombre}}</em><span></span></a></li>
            </ul>
            <div class="tab-content clearfix">
            <div id="home" class="tab-pane active">
                <div class="container" style="padding-top: 100px; !important">
                    <div class="form-group col-sm-11">
                        <label for="exampleInputPassword1" class="col-sm-11 col-form-label" style="text-align:left"> <b> Ingrese su correo asociado a su casilla electrnica</b></label>
                        <!-- <h4>Ingrese su correo asociado a su casilla electrnica</h4> -->
                    </div>
                    <div class="form-group col-sm-11">
                        <div class="row">
                          <label for="exampleInputPassword1" class="col-sm-2 col-form-label" style="text-align:center"> <b> Correo</b></label>
                          <div class="col-sm-8">
                              <input type="text" class="form-control" v-model="correoModelo">
                          </div>
                        </div>
                    </div>
                </div>
                
            </div>
            <div id="menu1" class="tab-pane ">
                <h3>Menu 1</h3>
                <p>Some content in menu 1.</p>
            </div>
            <div id="menu2" class="tab-pane ">
                <h3>Menu 2</h3>
                <p>Some content in menu 2.</p>
            </div>
        </div>
        <div class="abajo">
            <a href="#" class="previous float-left"> Salir X</a>
            <a href="#" @click="cambiarPaso()" class="next float-right">Continuar &raquo;</a>
        </div>
    </div>
        
        
    
</template>
<style >
a {
  text-decoration: none;
  display: inline-block;
  padding: 8px 16px;
}

a:hover {
  background-color: #ddd;
  color: black;
}

.previous {
  background-color: #f1f1f1;
  height: 10%;
  width: 25%;
  color: black;
}

.next {
  background-color: #4CAF50;
  height: 10%;
  width: 25%;
  color: white;
}
.padrePasos { 
    border: 1px solid rgb(206, 206, 206); 
    max-width:90% !important; 
    height: 800px;
    position: relative;
}
.abajo { 
    left: 0;
    position: absolute;
    text-align: center;
    bottom: 0;
    width: 100%;
}
.breadcrumb { 
  list-style: none; 
  overflow: hidden; 
  font: 18px Helvetica, Arial, Sans-Serif;
  margin: 40px;
  padding: 0;
}
.breadcrumb li { 
  float: left; 
}
.breadcrumb li a {
  color: white;
  text-decoration: none; 
  padding: 10px 0 10px 55px;
  background: brown; /* fallback color */
  background: hsla(34,85%,35%,1); 
  position: relative; 
  display: block;
  float: left;
}
.breadcrumb li a:after { 
  content: " "; 
  display: block; 
  width: 0; 
  height: 0;
  border-top: 50px solid transparent;           /* Go big on the size, and let overflow hide */
  border-bottom: 50px solid transparent;
  border-left: 30px solid hsla(34,85%,35%,1);
  position: absolute;
  top: 50%;
  margin-top: -50px; 
  left: 100%;
  z-index: 2; 
}	
.breadcrumb li a:before { 
  content: " "; 
  display: block; 
  width: 0; 
  height: 0;
  border-top: 50px solid transparent;           /* Go big on the size, and let overflow hide */
  border-bottom: 50px solid transparent;
  border-left: 30px solid white;
  position: absolute;
  top: 50%;
  margin-top: -50px; 
  margin-left: 1px;
  left: 100%;
  z-index: 1; 
}	
.breadcrumb li:first-child a {
  padding-left: 10px;
}
.breadcrumb li:nth-child(2) a       { background:        hsla(34,85%,45%,1); }
.breadcrumb li:nth-child(2) a:after { border-left-color: hsla(34,85%,45%,1); }
.breadcrumb li:nth-child(3) a       { background:        hsla(34,85%,55%,1); }
.breadcrumb li:nth-child(3) a:after { border-left-color: hsla(34,85%,55%,1); }
.breadcrumb li:nth-child(4) a       { background:        hsla(34,85%,65%,1); }
.breadcrumb li:nth-child(4) a:after { border-left-color: hsla(34,85%,65%,1); }
.breadcrumb li:nth-child(5) a       { background:        hsla(34,85%,75%,1); }
.breadcrumb li:nth-child(5) a:after { border-left-color: hsla(34,85%,75%,1); }
.breadcrumb li:last-child a {
  background: transparent !important;
  color: black;
  pointer-events: none;
  cursor: default;
}
.breadcrumb li:last-child a:after { border: 0; }
.breadcrumb li a:hover { background: hsla(34,85%,25%,1); }
.breadcrumb li a:hover:after { border-left-color: hsla(34,85%,25%,1) !important; }


.steps {
  margin: 0px;
  padding: 0;
  overflow: hidden;
}
.steps a {
  color: white;
  text-decoration: none;
}
.steps em {
  display: block;
  font-size: 1.1em;
  font-weight: bold;
}
.steps li {
  float: left;
  margin-left: 0;
  width: 25%; /* 100 / number of steps */
  height: 70px; /* total height */
  list-style-type: none;
  padding: 5px 5px 5px 30px; /* padding around text, last should include arrow width */
  border-right: 3px solid white; /* width: gap between arrows, color: background of document */
  position: relative;
}
/* remove extra padding on the first object since it doesn't have an arrow to the left */
.steps li:first-child {
  padding-left: 5px;
}
/* white arrow to the left to "erase" background (starting from the 2nd object) */
.steps li:nth-child(n+2)::before {
  position: absolute;
  top:0;
  left:0;
  display: block;
  border-left: 25px solid white; /* width: arrow width, color: background of document */
  border-top: 40px solid transparent; /* width: half height */
  border-bottom: 40px solid transparent; /* width: half height */
  width: 0;
  height: 0;
  content: " ";
}
/* colored arrow to the right */
.steps li::after {
  z-index: 1; /* need to bring this above the next item */
  position: absolute;
  top: 0;
  right: -25px; /* arrow width (negated) */
  display: block;
  border-left: 25px solid #7c8437; /* width: arrow width */
  border-top: 40px solid transparent; /* width: half height */
  border-bottom: 40px solid transparent; /* width: half height */
  width:0;
  height:0;
  content: " ";
}

/* Setup colors (both the background and the arrow) */

/* Completed */
.steps li { background-color: #7C8437; }
.steps li::after { border-left-color: #7c8437; }

/* Current */
.steps li.current { background-color: #C36615; }
.steps li.current::after { border-left-color: #C36615; }

/* Following */
.steps li.current ~ li { background-color: #EBEBEB; }
.steps li.current ~ li::after {	border-left-color: #EBEBEB; }

/* Hover for completed and current */
.steps li:hover {background-color: #696}
.steps li:hover::after {border-left-color: #696}



.arrows { white-space: nowrap; }
.arrows li {
    display: inline-block;
    line-height: 26px;
    margin: 0 9px 0 -10px;
    padding: 0 20px;
    position: relative;
}
.arrows li::before,
.arrows li::after {
    border-right: 1px solid #666666;
    content: '';
    display: block;
    height: 50%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    transform: skewX(45deg);   
}
.arrows li::after {
    bottom: 0;
    top: auto;
    transform: skewX(-45deg);
}

.arrows li:last-of-type::before, 
.arrows li:last-of-type::after { 
    display: none; 
}

.arrows li a { 
   font: bold 24px Sans-Serif;  
   letter-spacing: -1px; 
   text-decoration: none;
}

.arrows li:nth-of-type(1) a { color: hsl(0, 0%, 70%); } 
.arrows li:nth-of-type(2) a { color: hsl(0, 0%, 65%); } 
.arrows li:nth-of-type(3) a { color: hsl(0, 0%, 50%); } 
.arrows li:nth-of-type(4) a { color: hsl(0, 0%, 45%); } 
</style>
<script>
import axios from 'axios';
export default {
    name: 'Tramite',
    data(){
    return{
      nombreTipoTramite:'',
      idTipoTramite:0,
      correoModelo:'',
      listaPasos:null,
      pasoActual:0,
      currentClass:'current'
    }
  },
  mounted(){
    this.inicializar();
  },
  methods:{
    reemplazaVacio(cadena){
      return cadena==''?'-':cadena;
    },
    cambiarPaso(){
        var nuevoPaso=0;
        var indice=0;
        while(indice<this.listaPasos.length && nuevoPaso==0){
            if(this.listaPasos[indice].idPaso.idPasos>this.pasoActual)nuevoPaso=this.listaPasos[indice].idPaso.idPasos;
            indice++;
        }
        this.pasoActual=nuevoPaso;
        return nuevoPaso;
    },
    inicializar(){
        //var rutadev='http://localhost:8929/';  
        var rutaprod="https://mz-services.miraflores.gob.pe:8090/api/tramites/";
      axios.get(rutaprod+'tipotramite/inicializar/'+this.$route.params.idTipoTramite)
                    .then(response=>{
                      this.listaPasos=response.data.dataPasos;
                      this.pasoActual=response.data.primerPaso;
                      console.log(response.data.data);
                      console.log(this.listaPasos);
                      console.log('paso Actual: ',this.pasoActual);
                      //console.log(this.listaTipoTramite[0].nombre);
                  })
                  .catch(e=>console.log(e))
    }
    
  },computed:{
        claseLi(idPaso){
            var clase="";
            if(idPaso==this.pasoActual)clase="current";
            return clase;
        }
   }
}
</script>


